.ani-arrow {
  position: relative;
  padding-right: 30px;
  cursor: pointer;
  &::before {
    z-index: 3;
    content: '';
    position: absolute;
    right: 10px;
    // top: calc(50% - 1px) !important;
    top: 50%;
    width: 16px;
    height: 18px;
    background: url('../../../assets/images/home/base/ico_deg.png') no-repeat;
    background-size: 100% 100%;
    transform: translateY(-50%) rotate(-45deg);
    transition: all 0.2s ease;
  }
  &::after {
    display: none;
    z-index: 2;
    content: '';
    position: absolute;
    right: 8px;
    // top: calc(50% - 1px) !important;
    top: 50%;
    width: 13px;
    height: 18px;
    background: url('../../../assets/images/home/base/ico_line.png') no-repeat;
    background-size: 100% 100%;
    opacity: 0;
    transform: translateY(-50%);
    transition: all 0.2s linear;
  }
  &:hover {
    &::before {
      animation: roated 0.2s linear forwards;
    }
    // &::after {
    //   animation: lineShow 0.2s linear forwards;
    // }
  }
  &.ani-arrow-active {
    &::before {
      animation: roated 0.2s linear forwards;
    }
    // &::after {
    //   animation: lineShow 0.2s linear forwards;
    // }
  }
}
.ani-arrow-type2 {
  padding-right: 30px;
  &::before {
    right: 0;
    top: 50%;
    width: 10px;
    height: 12px;
    background: url('../../../assets/images/home/base/ico_deg2.png') no-repeat;
    background-size: 100% 100%;
  }
  &::after {
    right: 2px;
    top: 50%;
    width: 8px;
    height: 12px;
    background: url('../../../assets/images/home/base/ico_line2.png') no-repeat;
    background-size: 100% 100%;
  }
}

.ani-arrow-small {
  padding-right: 24px;
  &::before {
    right: 4px;
    width: 12px;
    height: 12px;
  }
}

@keyframes roated {
  0% {
    transform: translateY(-50%) rotate(-45deg);
  }

  to {
    transform: translateY(-50%) rotate(0);
  }
}

@keyframes reverseArrowRotateAndExtend {
  0% {
    transform: translateY(-50%) rotate(-45deg);
  }

  50% {
    right: 0;
    transform: translateY(-50%) rotate(0);
  }

  75% {
    right: 0;
    transform: translateY(-50%) rotate(0);
  }

  to {
    right: -7px;
    transform: translateY(-50%) rotate(0);
  }
}

@keyframes lineShow {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
