@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.homeChart {
  width: 100%;
  height: 100%;
  position: relative;
  .chart {
    width: 100%;
    height: 100%;
    position: relative;
  }
  .rate {
    position: absolute;
    right: 0;
    top: 0;
    font-family: Poppins;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: rgba(0, 255, 196, 1);
  }
  .rate_down {
    color: rgba(255, 50, 135, 1);
  }
}

@include mobile-only {
  .homeChart {
    .rate {
      right: initial;
      left: 10px;
      font-size: 12px;
    }
  }
}
