@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.title_wrap {
  text-align: center;
}
.title {
  opacity: 0;
  position: relative;
  font-family: Poppins;
  font-weight: 600;
  font-size: 42px;
  line-height: 63px;
  text-align: center;
  color: #fff;
  span {
    position: relative;
    &:first-child {
      &::before {
        display: none;
        content: '';
        position: absolute;
        left: -38px;
        top: 0;
        width: 28px;
        height: 26px;
        background: url(../../../assets/images/home/base/ico_sunToken_title.svg) no-repeat 0 0 / 100% 100%;
      }
    }
  }
  .title_end {
    position: relative;
    color: #ffe45b;
    .en-US {
    }
    &::after {
      display: none;
      content: '';
      position: absolute;
      top: 0;
      left: -5%;
      bottom: -40px;
      width: 115%;
      background-image: url(../../../assets/images/home/base/ico_circle.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
.sub_title {
  opacity: 0;
  margin-top: 20px;
  font-family: Poppins;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}

.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
}

.data {
  display: flex;
  margin-top: 65px;
  .market {
    position: relative;
    width: 380px;
    margin-right: 30px;
    .market_title {
      position: absolute;
      top: -12px;
      transform: translateY(-100%);
      display: flex;
      align-items: center;
      span {
        font-family: Poppins;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: rgba(108, 102, 134, 1);
      }
      .market_refresh {
        width: 17px;
        height: 17px;
        border-radius: 17px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgba(48, 34, 68, 1);
        cursor: pointer;
        span {
          margin-right: 10px;
        }
        &:hover {
          background-color: #8c51ff;
          img {
            transform: rotate(-360deg);
          }
        }
        img {
          display: block;
          width: 9px;
          height: 9px;
          transition: all 0.3s;
        }
      }
      .market_refresh_disabled {
        cursor: not-allowed;
        background: rgba(48, 34, 68, 1);
        &:hover {
          background: rgba(48, 34, 68, 1);
          img {
            transform: rotate(0);
          }
        }
      }
    }

    .market_list {
      .market_item {
        opacity: 0;
        position: relative;
        padding: 20px 20px 20px 30px;
        height: 120px;
        margin-bottom: 38px;
        background: rgba(18, 8, 34, 1);
        border-radius: 10px;
        // border: 1px solid rgba(255, 255, 255, 0.2);
        &:before {
          content: '';
          position: absolute;
          inset: 0; /*  */
          padding: 1px; /*  */
          border-radius: 8px; /*  */
          background: linear-gradient(181.14deg, rgba(255, 255, 255, 0.3) 25.9%, rgba(255, 255, 255, 0.1) 76.35%);
          -webkit-mask: linear-gradient(#fff, #fff) content-box, /*  */ linear-gradient(#fff, #fff); /*  */
          -webkit-mask-composite: xor; /*  */
          mask-composite: exclude; /*  Firefox */
          z-index: -1; /*  */
        }

        &:nth-child(2) {
          .item_name {
            .item_icon {
              background-image: url(../../../assets/images/home/sunToken/ico_market_2.png);
            }
          }
        }
        &:last-child {
          margin-bottom: 0;
          .item_name {
            .item_icon {
              background-image: url(../../../assets/images/home/sunToken/ico_market_3.png);
            }
          }
        }
        .item_name {
          display: flex;
          align-items: center;
          span {
            font-family: Poppins;
            font-weight: 500;
            font-size: 16px;
            line-height: 18px;
            margin-right: 10px;
            color: rgba(167, 162, 201, 1);
          }
          .item_icon {
            width: 16px;
            height: 16px;
            background: url(../../../assets/images/home/sunToken/ico_market_1.png) no-repeat 0 0 / 100% 100%;
          }
        }
        .item_num {
          margin-top: 17px;
          font-family: Poppins-Bold;
          font-weight: 700;
          font-size: 30px;
          line-height: 45px;
          color: #fff;
        }
        .item_chart {
          position: absolute;
          right: 20px;
          top: 20px;
          width: 130px;
          height: 80px;
        }
      }
    }
  }
  .burn {
    padding: 30px 0 0 30px;
    min-height: 426px;
    flex: 1;
    background: url(../../../assets/images/home/sunToken/bg.png) no-repeat 0 0 / 100% 100%;
    cursor: pointer;
    &:hover {
      background-image: url(../../../assets/images/home/sunToken/bg_active.png);
    }
    .burn_line {
      display: inline-block;
      margin: 28px 0 30px;
      width: 310px;
      height: 1px;
      // opacity: 0.15;
      // background: rgb(240, 232, 255);
      border-bottom: 1px dashed rgb(240, 232, 255, 0.15);
    }
    .burn_title1 {
      opacity: 0;
      display: inline-block;
      font-family: Poppins;
      font-weight: 600;
      font-size: 24px;
      line-height: 36px;
      color: #fff;
      & > div {
        padding-right: 35px;
      }
    }
    .burn_desc1 {
      opacity: 0;
      margin-top: 10px;
      margin-bottom: 30px;
      font-family: Poppins;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      color: rgba(108, 102, 134, 1);
    }
    .burn_title2 {
      opacity: 0;
      font-family: Poppins;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: rgba(108, 102, 134, 1);
    }

    .burn_num2 {
      opacity: 0;
      margin-top: 10px;
      font-family: Poppins;
      font-weight: 700;
      font-size: 30px;
      line-height: 45px;
      color: #fff;
      span {
        font-family: Poppins-Bold;
        &:first-child {
          margin-right: 10px;
        }
        &:last-child {
          font-weight: 900;
          font-size: 20px;
        }
      }
    }
    .burn_num3 {
      opacity: 0;
      margin-top: 4px;
      font-family: Poppins;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: rgba(108, 102, 134, 1);
    }
  }
}

.core {
  margin-top: 30px;
  .core_title {
    opacity: 0;
    position: relative;
    padding-left: 14px;
    font-family: Poppins;
    font-weight: 600;
    font-size: 30px;
    line-height: 45px;
    color: #fff;
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 4px;
      height: 15px;
      background: #ffe45b;
    }
  }
  .core_list {
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    .core_item {
      position: relative;
      opacity: 0;
      padding: 30px 30px 30px;
      border-radius: 10px;
      background: rgba(21, 12, 40, 0.1);
      border-radius: 10px;
      // background-clip: padding-box;
      // border: 1px solid transparent;
      // box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.2);
      backdrop-filter: blur(10px);

      &:before {
        content: '';
        position: absolute;
        inset: 0; /*  */
        padding: 1px; /*  */
        border-radius: 8px; /*  */
        background: linear-gradient(181.14deg, rgba(255, 255, 255, 0.3) 25.9%, rgba(255, 255, 255, 0.1) 76.35%);
        -webkit-mask: linear-gradient(#fff, #fff) content-box, /*  */ linear-gradient(#fff, #fff); /*  */
        -webkit-mask-composite: xor; /*  */
        mask-composite: exclude; /*  Firefox */
        z-index: -1; /*  */
      }

      .core_icon {
        display: block;
        width: 32px;
        height: 32px;
        background: url(../../../assets/images/home/sunToken/ico_use_1.png) no-repeat 0 0 / 100% 100%;
      }
      &:nth-child(2) {
        .core_icon {
          background-image: url(../../../assets/images/home/sunToken/ico_use_2.png);
        }
      }
      &:nth-child(3) {
        .core_icon {
          background-image: url(../../../assets/images/home/sunToken/ico_use_3.png);
        }
      }

      .core_item_name {
        margin-top: 20px;
        font-family: Poppins;
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        height: 72px;
        color: #fff;
      }
      .core_item_desc {
        margin-top: 20px;
        font-family: Poppins;
        font-weight: 400;
        font-size: 14px;
        line-height: 26px;
        color: rgba(167, 162, 201, 0.7);
      }
    }
  }
  .core_list_zh {
    .core_item {
      .core_item_name {
        height: 36px;
      }
    }
  }
}

.sunTokenActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
}
.dataActive {
  .market {
    .market_list {
      .market_item {
        animation: homeFlipInX 0.8s ease-in-out 0.2s forwards;
      }
    }
  }
  .burn {
    .burn_title1 {
      animation: homeFadeInUp 0.8s ease-in-out 0.3s forwards;
    }
    .burn_desc1 {
      animation: homeFadeInUp 0.8s ease-in-out 0.3s forwards;
    }
    .burn_title2 {
      animation: homeFadeInUp 0.8s ease-in-out 0.3s forwards;
    }
    .burn_num2 {
      animation: homeFadeInUp 0.8s ease-in-out 0.3s forwards;
    }
    .burn_num3 {
      animation: homeFadeInUp 0.8s ease-in-out 0.3s forwards;
    }
  }
}

.coreActive {
  .core_title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .core_list {
    .core_item {
      &:nth-child(1) {
        animation: homeFadeInUp2 0.8s ease-in-out 0.3s forwards;
      }
      &:nth-child(2) {
        animation: homeFadeInUp2 0.8s ease-in-out 0.3s forwards;
      }
      &:nth-child(3) {
        animation: homeFadeInUp2 0.8s ease-in-out 0.3s forwards;
      }
    }
  }
}

@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
    span {
      &:first-child {
        &::before {
          left: -20px;
          top: -12px;
        }
      }
    }
    .title_end {
      &::after {
        bottom: -22px;
      }
    }
  }
  .sub_title {
    margin-top: 10px;
    font-size: 14px;
    line-height: 21px;
  }
  .data {
    display: block;
    margin-top: 58px;
    .market {
      width: 100%;
      margin-right: 0;
      .market_title {
        position: absolute;
        top: -10px;
        span {
          font-size: 12px;
          line-height: 18px;
        }
        .market_refresh {
          span {
            margin-right: 4px;
          }
        }
      }

      .market_list {
        display: flex;
        .market_item {
          flex: 1;
          margin-right: 8px;
          width: 0;
          padding: 10px 0 58px 10px;
          // height: 130px;
          height: initial;
          margin-bottom: 0;
          border-radius: 6px;
          &:last-child {
            margin-right: 0;
          }

          .item_name {
            span {
              font-size: 12px;
              line-height: 18px;
              margin-right: 0;
            }
            .item_icon {
              display: none;
            }
          }
          .item_num {
            font-family: Poppins;
            font-weight: 600;
            margin-top: 10px;
            font-size: 16px;
            line-height: 24px;
          }
          .item_chart {
            position: absolute;
            right: 0;
            top: initial;
            bottom: 0;
            width: 100%;
            height: 58px;
          }
        }
      }
    }
    .burn {
      position: relative;
      margin-top: 10px;
      padding: 20px;
      min-height: initial;
      background: url(../../../assets/images/home/sunToken/bg_1_m.png) left 0 top 0 / 100% auto no-repeat,
        url(../../../assets/images/home/sunToken/bg_2_m.png) bottom 0 right 0 / 310px 310px no-repeat;
      background-color: rgba(18, 8, 34, 1);
      border-radius: 10px;
      &:hover {
        background: url(../../../assets/images/home/sunToken/bg_1_m.png) left 0 top 0 / 100% auto no-repeat,
          url(../../../assets/images/home/sunToken/bg_2_m.png) bottom 0 right 0 / 310px 310px no-repeat;
        background-color: rgba(18, 8, 34, 1);
      }
      &:before {
        content: '';
        position: absolute;
        inset: 0; /*  */
        padding: 1px; /*  */
        border-radius: 10px; /*  */
        background: linear-gradient(181.14deg, rgba(255, 255, 255, 0.3) 25.9%, rgba(255, 255, 255, 0.1) 76.35%);
        -webkit-mask: linear-gradient(#fff, #fff) content-box, /*  */ linear-gradient(#fff, #fff); /*  */
        -webkit-mask-composite: xor; /*  */
        mask-composite: exclude; /*  Firefox */
        z-index: 1; /*  */
      }
      .burn_line {
        display: block;
        margin: 16px 0 16px;
        width: 187px;
      }
      .burn_title1 {
        font-size: 20px;
        line-height: 30px;
      }
      .burn_desc1 {
        margin-top: 6px;
        margin-bottom: 20px;
        font-size: 12px;
        line-height: 18px;
      }
      .burn_title2 {
        font-size: 14px;
        line-height: 21px;
      }

      .burn_num2 {
        font-size: 24px;
        line-height: 36px;
        span {
          &:first-child {
            margin-right: 6px;
          }
          &:last-child {
            font-size: 16px;
          }
        }
      }
      .burn_num3 {
        margin-top: 2px;
        font-size: 14px;
        line-height: 21px;
      }
    }
  }

  .core {
    margin-top: 20px;
    .core_title {
      padding-left: 10px;
      font-size: 20px;
      line-height: 30px;
      &::before {
        width: 2px;
        height: 12px;
      }
    }
    .core_list {
      margin-top: 10px;
      display: block;
      .core_item {
        padding: 10px 10px 20px;
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
        .core_icon {
          width: 20px;
          height: 20px;
        }
        .core_item_name {
          margin-top: 10px;
          font-size: 16px;
          line-height: 24px;
          height: initial;
        }
        .core_item_desc {
          margin-top: 6px;
          font-size: 14px;
          line-height: 21px;
        }
      }
    }
  }
}

@media (max-width: 367px) {
  .data {
    .market {
      .market_list {
        .market_item {
          .item_name {
            font-size: 10px;
          }
          .item_num {
            font-size: 12px;
          }
        }
      }
    }
  }
}
