@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.title_wrap {
  text-align: center;
}
.title {
  opacity: 0;
  position: relative;
  font-family: Poppins;
  font-weight: 600;
  font-size: 42px;
  line-height: 63px;
  text-align: center;
  color: #fff;
  span {
    position: relative;
    &:first-child {
      &::before {
        display: none;
        content: '';
        position: absolute;
        left: -66px;
        top: 0;
        width: 46px;
        height: 32px;
        background: url(../../../assets/images/home/base/ico_pools_title.svg) no-repeat 0 0 / 100% 100%;
      }
    }
  }
  .title_end {
    position: relative;
    color: #ffe45b;
    .en-US {
    }
    &::after {
      display: none;
      content: '';
      position: absolute;
      top: 0;
      left: -5%;
      bottom: -40px;
      width: 115%;
      background-image: url(../../../assets/images/home/base/ico_circle.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
.sub_title {
  opacity: 0;
  margin-top: 20px;
  font-family: Poppins;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}

.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
}

.pools {
  .pc_container {
    // opacity: 0;
  }
  .list {
    margin-top: 50px;
    height: 296px;
  }
  .card {
    position: relative;
    margin-right: 30px;
    padding: 20px 20px 30px;
    width: 380px;
    height: 296px;
    border: 1px solid;
    border-image-source: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0.05) 100%);
    background: url(../../../assets/images/home/pools/bg.png) no-repeat;
    background-position: 0 0;
    background-size: 100% 100%;
    overflow: hidden;
    &:hover {
      background-image: url(../../../assets/images/home/pools/bg_active.png);
    }
  }
  .tag {
    position: absolute;
    right: -1px;
    top: -1px;
    padding: 3px 10px;
    font-family: Poppins;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    color: rgba(195, 177, 255, 1);
    background: rgba(213, 171, 255, 0.2);
    border-radius: 0 10px 0 10px;
  }

  .name {
    margin-top: 10px;
    font-family: Poppins;
    font-weight: 600;
    font-size: 20px;
    height: 30px;
    line-height: 30px;
    color: #fff;
    text-align: right;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .icon {
    position: absolute;
    left: 30px;
    top: 30px;
    .icon_img {
      width: 42px;
      height: 42px;
      border-radius: 42px;
      background: #fff;
      overflow: hidden;
      &:last-child {
        position: absolute;
        left: 20px;
        top: 20px;
      }
    }
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .tvl {
    margin-top: 10px;
    font-family: Poppins;
    font-weight: 600;
    font-size: 14px;
    height: 21px;
    line-height: 21px;
    text-align: right;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .tvl_label {
    margin-right: 8px;
    color: rgba(108, 102, 134, 1);
  }
  .tvl_num {
    color: #fff;
  }
  .apy {
    display: flex;
    justify-content: space-between;
    margin-top: 39px;
    height: 48px;
  }
  .apy_label {
    position: relative;
    font-family: Poppins;
    font-weight: 500;
    font-size: 14px;
    line-height: 59px;
    color: rgba(108, 102, 134, 1);
  }
  .apy_value {
    font-family: Poppins-Bold;
    font-weight: 600;
    font-size: 32px;
    line-height: 48px;
    color: rgba(0, 222, 171, 1);
  }
  .tip {
    margin-top: 17px;
    font-family: Poppins;
    font-weight: 500;
    font-size: 12px;
    height: 18px;
    line-height: 18px;
    color: rgba(108, 102, 134, 1);
    text-align: center;
  }
  .btn {
    margin-top: 10px;
  }
  .m_container {
    .card {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .more_wrap {
    margin-top: 20px;
    text-align: center;
    .more {
      display: inline-flex;
      align-items: center;
      span {
        margin-right: 6px;
        font-family: Poppins;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: rgba(191, 171, 255, 1);
      }
      img {
        display: block;
        width: 12px;
        height: 12px;
        transition: all 0.3s;
      }
    }
    .more_active {
      img {
        transform: rotate(180deg);
      }
    }
  }
}

.poolsActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
  // .pc_container {
  //   animation: homeFadeInBottomRight2 0.8s ease-in-out 1s forwards;
  // }
}

.marque_container {
  opacity: 0;
}
.pc_container_active {
  .marque_container {
    animation: homeFadeInBottomRight2 0.6s ease-in-out forwards;
  }
}

@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
    span {
      &:first-child {
        &::before {
          left: -40px;
          top: -22px;
        }
      }
    }
    .title_end {
      &::after {
        bottom: -22px;
      }
    }
  }
  .sub_title {
    margin-top: 10px;
    font-size: 14px;
    line-height: 21px;
  }
  .pools {
    .list {
      margin-top: 40px;
      height: initial;
    }
    .card {
      margin-right: 0;
      padding: 20px;
      width: 100%;
      height: initial;
      border: 1px solid;
      border-radius: 10px;
      overflow: hidden;
      // background: rgb(19, 6, 46, 0.5);
      background: linear-gradient(122.68deg, rgba(19, 6, 46, 0.5) 14.19%, rgba(26, 14, 44, 0.5) 85.09%);
      border: 1px solid rgba(255, 255, 255, 0.1);
      &:hover {
        background: linear-gradient(122.68deg, rgba(19, 6, 46, 0.5) 14.19%, rgba(26, 14, 44, 0.5) 85.09%);
      }
    }
    .name {
      font-size: 16px;
      height: 24px;
      line-height: 24px;
    }
    .icon {
      top: 20px;
      left: 20px;
      .icon_img {
        width: 50px;
        height: 50px;
        border-radius: 50px;
        &:last-child {
          position: absolute;
          left: 24px;
          top: 24px;
        }
      }
    }
    .name {
      padding-left: 80px;
    }
    .tvl {
      white-space: wrap;
      height: initial;
      padding-left: 80px;
      word-break: break-all;
    }
    .apy {
      margin-top: 30px;
      height: 36px;
    }
    .apy_label {
      line-height: 36px;
    }
    .apy_value {
      font-size: 24px;
      line-height: 36px;
    }
    .tip {
      margin-top: 14px;
    }
  }
}
