@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.btn {
  line-height: 40px;
  border-radius: 6px;
  font-family: Poppins;
  font-weight: 600;
  font-size: 14px;
  height: 40px;
  line-height: 40px;
  color: rgba(191, 171, 255, 1);
  background: rgba(174, 154, 255, 0.15);
  cursor: pointer;
  transition: all 0.3s;
  text-align: center;
  -webkit-tap-highlight-color: transparent;
  &:hover,
  &:active {
    color: #280065;
    background: rgba(174, 154, 255, 1);
    // box-shadow: 0px -2px 0px 0px rgba(9, 1, 23, 0.3) inset;
  }
}

@include mobile-only {
  .btn {
    height: 36px;
    line-height: 36px;
    font-size: 16px;
  }
}
