.custom-tooltip {
  box-sizing: border-box;
  padding: 0 8px;
  background: rgba(61, 148, 113, 0.1);
  backdrop-filter: blur(10px);
  height: 24px;
  border-radius: 24px;
  white-space: nowrap;
  border: 1px solid rgba(255, 255, 255, 0.1);
  .label {
    margin-right: 8px;
    font-family: Poppins;
    font-weight: 400;
    font-style: Regular;
    font-size: 12px;
    line-height: 22px;
    color: rgba(167, 162, 201, 1);
  }
  .intro {
    font-family: Poppins;
    font-weight: 600;
    font-weight: 500;
    font-size: 12px;
    line-height: 22px;
    color: #fff;
  }
}
