@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.title_wrap {
  text-align: center;
}
.title {
  opacity: 0;
  position: relative;
  font-family: Poppins;
  font-weight: 600;
  font-size: 42px;
  line-height: 63px;
  text-align: center;
  color: #fff;
  span {
    position: relative;
    &:first-child {
      &::before {
        display: none;
        content: '';
        position: absolute;
        left: -43px;
        top: 0;
        width: 34px;
        height: 38px;
        background: url(../../../assets/images/home/base/ico_notice_title.svg) no-repeat 0 0 / 100% 100%;
      }
    }
  }
  .title_end {
    position: relative;
    color: #ffe45b;
    .en-US {
    }
    &::after {
      display: none;
      content: '';
      position: absolute;
      top: 0;
      left: -10%;
      bottom: -30px;
      width: 140%;
      background-image: url(../../../assets/images/home/base/ico_circle_notice_en.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
.sub_title {
  opacity: 0;
  margin-top: 23px;
  font-family: Poppins;
  font-weight: 400;
  font-size: 20px;
  line-height: 36px;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}
.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
}

.notice {
  .list {
    position: relative;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    padding-top: 44px;
    .more {
      opacity: 0;
      position: absolute;
      right: 0;
      top: 3px;
      font-family: Poppins;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: rgba(255, 255, 255, 1);
    }
    .item {
      opacity: 0;
      position: relative;
      height: 380px;
      border-radius: 10px;
      background: linear-gradient(180deg, rgba(50, 36, 72, 0) 50%, rgba(36, 23, 55, 0.8) 65.24%, #1a0e2c 100%);
      cursor: pointer;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        transition: all 0.3s ease-in-out;
      }
      &:hover {
        .item_title {
          bottom: 60px;
          // height: 96px;
          // line-height: 32px;
          // font-size: 20px;
        }
        img {
          transform: scale(1.05);
        }
      }
    }
    .item_title {
      opacity: 0;
      z-index: 3;
      position: absolute;
      left: 0;
      bottom: 49px;
      width: 100%;
      height: 72px;
      padding: 0 20px;
      font-family: Poppins;
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      color: #fff;
      transition: all 0.3s ease-in-out;
    }
    .item_date {
      opacity: 0;
      z-index: 4;
      position: absolute;
      left: 0;
      bottom: 18px;
      width: 100%;
      padding: 0 20px;
      font-family: Poppins;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      color: rgba(255, 255, 255, 0.7);
    }
    .mask {
      z-index: 2;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(50, 36, 72, 0) 50%, rgba(36, 23, 55, 0.8) 65.24%, #1a0e2c 100%);
    }
  }
}

.noticeActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
}

.listActive {
  .more {
    animation: homeZoomIn 0.5s ease-in-out 1.2s forwards;
  }
  .item {
    animation: homeFadeIn 0.8s ease-in-out 0.4s forwards;
    .item_title {
      animation: homeFadeInUp 0.8s ease-in-out 0.8s forwards;
    }
    .item_date {
      animation: homeFadeInUp 0.8s ease-in-out 0.8s forwards;
    }
  }
}

@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
    span {
      &:first-child {
        &::before {
          left: -34px;
          top: -18px;
        }
      }
    }
    .title_end {
      &::after {
        bottom: -22px;
      }
    }
  }
  .sub_title {
    margin-top: 10px;
    font-size: 14px;
    line-height: 20px;
  }

  .notice {
    .more {
      font-size: 14px;
    }
    .list {
      display: block;
      margin-top: 8px;
      padding-top: 30px;

      // a {
      //   display: block;
      //   border-radius: 10px;
      //   height: 240px;
      //   overflow: hidden;
      // }

      .item {
        z-index: 1;
        position: relative;
        margin-bottom: 10px;
        height: 240px;
        animation: none;
        opacity: 1;
        // overflow: initial;
        border-radius: 10px;
        border: 1px solid rgba(255, 255, 255, 0.1);
        img {
          height: initial;
        }
        &::before {
          display: none;
          content: '';
          position: absolute;
          top: -1px; /*  */
          left: -1px;
          right: -1px;
          bottom: -1px;
          z-index: -1; /* 3 */
          border-radius: 10px; /* 1px */
          background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0.05) 100%);
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
      .item_title {
        font-size: 16px;
        line-height: 24px;
        height: 48px;
        -webkit-line-clamp: 2;
        animation: none;
        opacity: 1;
      }
      .item_date {
        font-size: 12px;
        line-height: 18px;
        animation: none;
        opacity: 1;
      }
    }
  }
}
