@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';

.top_swap {
  display: flex;
  .top_swap_l {
    flex: 1;
    margin-right: 40px;
    margin-top: 50px;
    .title {
      height: 75px;
      img {
        display: block;
        height: 100%;
      }
    }
    .marquee_wrap {
      display: block;
      margin-top: 9px;
    }
    .marquee {
      display: inline-flex;
      align-items: center;
      .marquee_label {
        margin-right: 20px;
        img {
          display: block;
          height: 38px;
        }
      }
      .marquee_area {
        display: inline-flex;
        align-items: center;
        .marquee_area_l,
        .marquee_area_r {
          img {
            display: block;
            height: 34px;
          }
        }
        .marquee_area_r {
          margin-left: 10px;
        }
        .marquee_area_c {
          height: 75px;
          transition: width 0.3s;
        }
      }
    }
    .desc {
      margin-top: 30px;
      font-family: Poppins;
      font-weight: 500;
      font-size: 22px;
      line-height: 30px;
      color: #fff;
      user-select: none;
    }
    .tag {
      display: inline-flex;
      align-items: center;
      margin-top: 40px;
      padding: 0 10px;
      font-family: Poppins;
      font-weight: 400;
      font-size: 12px;
      height: 40px;
      line-height: 40px;
      color: rgba(221, 216, 255, 1);
      background: linear-gradient(rgba(224, 208, 255, 0.1), rgba(224, 208, 255, 0.1)),
        url(../../../assets/images/home/topSwap/bg.png) right / auto 100% no-repeat;
      border-radius: 4px;
      .tag_icon {
        margin-right: 10px;
        width: 24px;
        height: 30px;
        background: url(../../../assets/images/home/topSwap/ico_dnk.png) no-repeat no-repeat 0 0 / 100% 100%;
      }
    }
  }
  .top_swap_r {
    width: 600px;
    // min-height: 502px;
    // // background: rgba(20, 11, 35, 1);
    // border-radius: 20px;
    // border: 1px solid transparent;
    // // overflow: hidden;
    // background: linear-gradient(white, white) padding-box, linear-gradient(var(--angle), #6577ff, #6400ff) border-box;
    // --angle: 0deg;
    // animation: 4s home_rotate linear infinite;
  }
  .swap_wrapper {
    z-index: 0;
    position: relative;
    flex: 1;
    width: 100%;
    padding: 0 20px;
    border-radius: 20px;
    overflow: hidden;

    //&::before {
    //  content: '';
    //  position: absolute;
    //  z-index: -2;
    //  left: -50%;
    //  top: -50%;
    //  width: 200%;
    //  height: 200%;
    //  background-color: transparent;
    //  background-repeat: no-repeat;
    //  background-size: 50% 50%, 50% 50%;
    //  background-position: 0 0, 100% 0, 100% 100%, 0 100%;
    //  background-image: linear-gradient(
    //      90deg,
    //      rgba(152, 137, 181, 0) 0%,
    //      rgba(152, 137, 181, 0) 40%,
    //      rgba(152, 137, 181, 0) 60%,
    //      rgba(152, 137, 181, 0) 70%,
    //      rgba(152, 137, 181, 0) 80%,
    //      rgba(152, 137, 181, 1) 90%,
    //      rgba(152, 137, 181, 0) 100%
    //    ),
    //    linear-gradient(transparent, transparent), linear-gradient(transparent, transparent),
    //    linear-gradient(transparent, transparent);
    //  animation: rotate 6s linear infinite;
    //}

    &::after {
      content: '';
      position: absolute;
      z-index: -1;
      left: 1px;
      top: 1px;
      width: calc(100% - 2px);
      height: calc(100% - 2px);
      background: rgba(27, 13, 49, 1);
      border-radius: 20px;
      border: 1px solid rgba(255, 255, 255, 0.05);
    }
  }
  .swap_wrapper_inner {
    position: relative;
    z-index: 10;
  }

  .marquee_full_wrap {
    height: 75px;
    img {
      display: block;
      height: 75px;
    }
  }
}

@keyframes rotate {
  100% {
    transform: rotate(1turn);
  }
}

@keyframes home_rotate {
  to {
    --angle: 360deg;
  }
}

@property --angle {
  syntax: '<angle>';
  initial-value: 0deg;
  inherits: false;
}

@include mobile-only {
  .top_swap {
    display: block;
    .top_swap_l {
      margin-top: 10px;
      margin-right: 0;
      .title {
        padding-left: 10px;
        height: 45px;
      }
      .marquee {
        padding-left: 10px;
        margin-top: 0;
        .marquee_label {
          margin-right: 10px;
          height: 22px;
          img {
            height: 22px;
          }
        }
        .marquee_area {
          .marquee_area_l,
          .marquee_area_r {
            img {
              height: 23px;
            }
          }
          .marquee_area_r {
            margin-left: 5px;
          }
          .marquee_area_c {
            height: 45px;
            overflow: hidden;
          }
        }
        .marquee_row {
          height: 45px;
          .marquee_row_icon {
            margin-right: 6px;
            width: 38px;
            height: 38px;
          }
          .marquee_row_text {
            font-size: 30px;
            height: 45px;
            line-height: 45px;
          }
        }
      }
      .desc {
        padding-left: 10px;
        margin-top: 16px;
        font-size: 14px;
        line-height: 22px;
      }
      .tag {
        display: none;
      }
    }
    .top_swap_r {
      margin-top: 30px;
      width: 100%;
      min-height: initial;
      border-radius: 20px;
    }
    .swap_wrapper {
      padding: 0 10px;
      border-radius: 10px;
      &::after {
        border-radius: 10px;
      }
    }
    .marquee_full_wrap {
      height: 45px;
      img {
        display: block;
        height: 45px;
      }
    }
  }
}
