@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.title_wrap {
  text-align: center;
}
.title {
  opacity: 0;
  position: relative;
  font-family: Poppins;
  font-weight: 600;
  font-size: 42px;
  line-height: 63px;
  text-align: center;
  color: #fff;
  span {
    position: relative;
    &:first-child {
      &::before {
        display: none;
        content: '';
        position: absolute;
        left: -46px;
        top: 0;
        width: 26px;
        height: 26px;
        background: url(../../../assets/images/home/base/ico_defiCard_title.svg) no-repeat 0 0 / 100% 100%;
      }
    }
  }
  .title_end {
    position: relative;
    color: #ffe45b;
    .en-US {
    }
    &::after {
      display: none;
      content: '';
      position: absolute;
      top: 0;
      left: -5%;
      bottom: -40px;
      width: 115%;
      background-image: url(../../../assets/images/home/base/ico_circle.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
.sub_title {
  opacity: 0;
  margin-top: 20px;
  font-family: Poppins;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}

.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
}

.defiCard {
  .list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    margin-top: 50px;
    .item {
      position: relative;
      padding: 10px 10px 30px;
      height: 456px;
      // background: rgba(24, 18, 40, 0.5);
      // border-radius: 20px;
      // backdrop-filter: blur(2px);
      background: url('../../../assets/images/home/defiCard/bg.png') no-repeat;
      background-size: 100% 100%;
      &:before {
        display: none;
        content: '';
        position: absolute;
        inset: 0; /*  */
        padding: 1px; /*  */
        border-radius: 10px; /*  */
        background: linear-gradient(181.14deg, rgba(255, 255, 255, 0.3) 25.9%, rgba(255, 255, 255, 0.1) 76.35%);
        -webkit-mask: linear-gradient(#fff, #fff) content-box, /*  */ linear-gradient(#fff, #fff); /*  */
        -webkit-mask-composite: xor; /*  */
        mask-composite: exclude; /*  Firefox */
        z-index: -1; /*  */
      }
      // cursor: pointer;
      .item_main {
        padding: 0 10px;
      }
      &:hover {
        background: url('../../../assets/images/home/defiCard/bg_active.png') no-repeat;
        background-size: 100% 100%;
      }
      .item_card {
        position: relative;
        height: 170px;
      }
      .item_title {
        font-family: Poppins;
        font-weight: 600;
        font-size: 22px;
        line-height: 33px;
        color: #fff;
        margin-top: 10px;
        span {
          opacity: 0;
        }
      }
      .item_tags_list {
        opacity: 0;
        display: inline-flex;
        margin-top: 17px;
        .item_tag {
          margin-right: 10px;
          padding: 3px 6px;
          font-family: Poppins;
          font-weight: 400;
          font-size: 12px;
          line-height: 18px;
          border-radius: 4px;
          background: rgb(158, 154, 188, 0.15);
          color: rgba(158, 154, 188, 1);
          &:last-child {
            margin-right: 0;
          }
        }
      }
      .item_desc {
        opacity: 0;
        font-family: Poppins;
        font-weight: 400;
        font-size: 14px;
        margin-top: 20px;
        height: 66px;
        line-height: 22px;
        color: rgba(158, 154, 188, 1);
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .btn {
        opacity: 0;
        margin-top: 30px;
      }
      &:nth-child(1) {
        .item_card_icon1 {
          z-index: 1;
          position: absolute;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background-image: url('../../../assets/images/home/defiCard/g1_1.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
        }
        .item_card_icon2 {
          z-index: 2;
          position: absolute;
          left: 10px;
          top: 70px;
          width: 70px;
          height: 70px;
          background-image: url('../../../assets/images/home/defiCard/g1_2.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon3 {
          z-index: 3;
          position: absolute;
          right: 40px;
          top: 13px;
          width: 60px;
          height: 60px;
          background-image: url('../../../assets/images/home/defiCard/g1_3.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon4 {
          z-index: 4;
          position: absolute;
          right: 20px;
          bottom: 0;
          width: 55px;
          height: 55px;
          background-image: url('../../../assets/images/home/defiCard/g1_4.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        &:hover {
          .item_card_icon2 {
            left: -10px;
          }
          .item_card_icon3 {
            right: 20px;
          }
          .item_card_icon4 {
            right: 50px;
          }
        }
      }
      &:nth-child(2) {
        .item_card_icon1 {
          z-index: 2;
          position: absolute;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background-image: url('../../../assets/images/home/defiCard/g2_1.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
        }
        .item_card_icon2 {
          z-index: 4;
          position: absolute;
          left: 65px;
          top: 55px;
          width: 60px;
          height: 60px;
          background-image: url('../../../assets/images/home/defiCard/g2_2.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon3 {
          z-index: 3;
          position: absolute;
          left: 100px;
          top: 25px;
          width: 70px;
          height: 70px;
          background-image: url('../../../assets/images/home/defiCard/g2_3.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon4 {
          z-index: 5;
          position: absolute;
          right: 72px;
          top: 49px;
          width: 70px;
          height: 70px;
          background-image: url('../../../assets/images/home/defiCard/g2_4.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon5 {
          z-index: 1;
          position: absolute;
          right: 4px;
          top: 25px;
          width: 80px;
          height: 97px;
          background-image: url('../../../assets/images/home/defiCard/g2_5.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        &:hover {
          .item_card_icon2 {
            transform: scale(1.1);
          }
          .item_card_icon3 {
            transform: scale(1.1);
          }
          .item_card_icon4 {
            transform: scale(1.1);
          }
          .item_card_icon5 {
            right: -4px;
            top: 7px;
            transform: scale(1.2);
          }
        }
      }
      &:nth-child(3) {
        .item_card_icon1 {
          z-index: 3;
          position: absolute;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background-image: url('../../../assets/images/home/defiCard/g3_1.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
        }
        .item_card_icon2 {
          z-index: 2;
          position: absolute;
          left: 5px;
          top: 20px;
          width: 100px;
          height: 126px;
          background-image: url('../../../assets/images/home/defiCard/g3_2.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon3 {
          z-index: 4;
          position: absolute;
          left: 72px;
          top: -17px;
          width: 140px;
          height: 140px;
          background-image: url('../../../assets/images/home/defiCard/g3_3.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon4 {
          z-index: 1;
          position: absolute;
          right: 5px;
          top: 20px;
          width: 120px;
          height: 130px;
          background-image: url('../../../assets/images/home/defiCard/g3_4.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }

        &:hover {
          .item_card_icon2 {
            transform: rotateZ(20deg);
            transform-origin: center;
          }
          .item_card_icon3 {
            transform: rotateZ(-15deg) scale(1.13);
          }
          .item_card_icon4 {
            right: 0px;
            top: 15px;
            transform: scale(1.2);
          }
        }
      }
      &:nth-child(4) {
        .item_card_icon1 {
          z-index: 2;
          position: absolute;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background-image: url('../../../assets/images/home/defiCard/g4_1.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
        }
        .item_card_icon2 {
          z-index: 3;
          position: absolute;
          left: 60px;
          bottom: 10px;
          width: 40px;
          height: 42px;
          background-image: url('../../../assets/images/home/defiCard/g4_2.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon3 {
          z-index: 4;
          position: absolute;
          left: 72px;
          top: 0;
          width: 120px;
          height: 120px;
          background-image: url('../../../assets/images/home/defiCard/g4_3.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        .item_card_icon4 {
          z-index: 1;
          position: absolute;
          right: 25px;
          top: 65px;
          width: 120px;
          height: 98px;
          background-image: url('../../../assets/images/home/defiCard/g4_4.png');
          background-size: 100% 100%;
          background-repeat: no-repeat;
          transition: all 0.3s ease-in-out;
        }
        &:hover {
          .item_card_icon2 {
            left: 55px;
            bottom: 25px;
            width: 26px;
            height: 30px;
          }
          .item_card_icon3 {
            transform: scale(1.1);
          }
          .item_card_icon4 {
            right: 10px;
            top: 50px;
          }
        }
      }
    }
    .m_img {
      display: none;
    }
  }
}

.defiCardActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
}

.defiCard {
  .listActive {
    .item {
      .item_title {
        span {
          display: inline-block;
          animation: homeFadeInUp 0.6s ease-in-out forwards;
        }
      }
      .item_tags_list {
        animation: homeFadeInUp 0.6s ease-in-out 0.4s forwards;
      }
      .item_desc {
        animation: homeFadeInUp 0.6s ease-in-out 1s forwards;
      }
      .btn {
        animation: homeFadeIn 0.6s ease-in-out 1.6s forwards;
      }
    }
  }
}

// .dayStatusInview {
//   animation: homeFadeIn 1s ease-in-out forwards;
// }
@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
    span {
      &:first-child {
        &::before {
          left: -26px;
          top: -13px;
        }
      }
    }
    .title_end {
      &::after {
        bottom: -22px;
      }
    }
  }
  .sub_title {
    margin-top: 10px;
    font-size: 14px;
    line-height: 21px;
  }

  .defiCard {
    .list {
      grid-template-columns: 1fr;
      margin-top: 20px;
      .m_img {
        display: block;
        img {
          display: block;
          width: 100%;
        }
      }
      .item {
        position: relative;
        padding: 10px 10px 20px;
        height: initial;
        // background: rgb(19, 6, 46, 0.5);
        background: linear-gradient(122.68deg, rgba(19, 6, 46, 0.5) 14.19%, rgba(26, 14, 44, 0.5) 85.09%);
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-radius: 10px;
        &:hover {
          background: linear-gradient(122.68deg, rgba(19, 6, 46, 0.5) 14.19%, rgba(26, 14, 44, 0.5) 85.09%);
        }
        .item_main {
          margin-top: 7px;
        }
        .item_card {
          height: initial;
        }
        .item_top {
          display: flex;
          align-items: center;
        }
        .item_title {
          font-size: 20px;
          line-height: 24px;
          margin-top: 0;
          margin-right: 10px;
        }
        .item_tags_list {
          margin-top: 0;
          .item_tag {
            padding: 0 6px;
            margin-right: 6px;
            line-height: 24px;
            &:last-child {
              margin-right: 0;
            }
          }
        }
        .item_desc {
          margin-top: 16px;
          height: 44px;
          -webkit-line-clamp: 2;
        }
        .btn {
          margin-top: 20px;
        }
        .item_card_icon1,
        .item_card_icon2,
        .item_card_icon3,
        .item_card_icon4,
        .item_card_icon5 {
          display: none;
        }
      }
    }
    .listActive {
      .item {
        .item_title {
          span {
            animation: homeFadeInUp 0.8s ease-in-out forwards;
          }
        }
        .item_tags_list {
          animation: homeFadeInUp 0.8s ease-in-out 0.6s forwards;
        }
        .item_desc {
          animation: homeFadeInUp 0.8s ease-in-out 1.2s forwards;
        }
        .btn {
          animation: homeFadeInUp 0.8s ease-in-out 1.8s forwards;
        }
      }
    }
  }
}
