@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.title_wrap {
  text-align: center;
}
.title {
  opacity: 0;
  position: relative;
  font-family: Poppins;
  font-weight: 600;
  font-size: 42px;
  line-height: 63px;
  text-align: center;
  color: #fff;
  span {
    position: relative;
    &:first-child {
      &::before {
        display: none;
        content: '';
        position: absolute;
        left: -50px;
        top: 0;
        width: 30px;
        height: 30px;
        background: url(../../../assets/images/home/base/ico_partner_title.svg) no-repeat 0 0 / 100% 100%;
      }
    }
  }
  .title_end {
    position: relative;
    color: #ffe45b;
    .en-US {
    }
    &::after {
      display: none;
      content: '';
      position: absolute;
      top: 0;
      left: -5%;
      bottom: -40px;
      width: 115%;
      background-image: url(../../../assets/images/home/base/ico_circle.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
.sub_title {
  opacity: 0;
  margin-top: 20px;
  font-family: Poppins;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}
.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
  .title_end {
    &::after {
      left: -8%;
      bottom: -38px;
      width: 130%;
    }
  }
}

.partner {
  .pc_container {
    opacity: 0;
    padding: 70px 0 0;
    height: 150px;
    .marque_container {
      .img_wrapper {
        display: flex;
        box-sizing: border-box;
        align-items: center;
        margin: 0 15px;
        height: 80px;
        padding: 0 34px;
        border: 1px solid rgba(255, 255, 255, 0.3);
        border-radius: 80px;
        cursor: pointer;
        &:hover {
          border-color: rgba(255, 255, 255, 0.5);
          .active {
            display: block;
          }
          .default {
            display: none;
          }
        }
      }
      .active {
        display: none;
      }
    }
  }

  .m_container {
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5px;
    & > div {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 46px;
      border: 1px solid rgba(255, 255, 255, 0.1);
      background: rgba(24, 18, 40, 1);
      border-radius: 6px;
      img {
        display: block;
        height: 26px;
      }
    }
  }
}

.partnerActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
}
.partner {
  .pc_container_active {
    animation: homeFadeInBottomRight2 0.8s ease-in-out 0.4s forwards;
  }
}

@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
    span {
      &:first-child {
        &::before {
          left: -20px;
          top: -20px;
        }
      }
    }
    .title_end {
      &::after {
        bottom: -22px;
      }
    }
  }
  .sub_title {
    margin-top: 20px;
    font-size: 14px;
    line-height: 20px;
  }
}
