@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.title_wrap {
  text-align: center;
}
.title {
  opacity: 0;
  position: relative;
  font-family: Poppins;
  font-weight: 600;
  font-size: 42px;
  line-height: 63px;
  text-align: center;
  color: #fff;
  span {
    position: relative;
    &:first-child {
      &::before {
        display: none;
        content: '';
        position: absolute;
        left: -44px;
        top: 0;
        width: 34px;
        height: 30px;
        background: url(../../../assets/images/home/base/ico_develop_title.svg) no-repeat 0 0 / 100% 100%;
      }
    }
  }
  .title_end {
    position: relative;
    color: #ffe45b;
    .en-US {
    }
    &::after {
      display: none;
      content: '';
      position: absolute;
      top: 0;
      left: -5%;
      bottom: -40px;
      width: 115%;
      background-image: url(../../../assets/images/home/base/ico_circle.svg);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
.sub_title {
  opacity: 0;
  margin-top: 23px;
  font-family: Poppins;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
}
.zh_wrap {
  .title {
    letter-spacing: 3px;
  }
}

.develop {
  .list_wrap {
    margin-top: 50px;
    .list {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    .item {
      opacity: 0;
      position: relative;
      height: 240px;
      background: url('../../../assets/images/home/develop/bg_develop.png') no-repeat;
      background-size: 100% 100%;
      cursor: pointer;
      &:hover {
        background-image: url('../../../assets/images/home/develop/bg_develop_active.png');
        .item_icon {
          opacity: 1;
        }
      }
      .item_icon {
        position: absolute;
        left: 20px;
        top: 20px;
        width: 40px;
        height: 40px;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        opacity: 0.7;
      }
      .item_card {
        z-index: 1;
        position: absolute;
        right: 10px;
        bottom: 20px;
        width: 260px;
        height: 200px;
      }
      .item_title {
        opacity: 0;
        z-index: 2;
        position: absolute;
        left: 30px;
        bottom: 82px;
        font-family: Poppins;
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        color: #fff;
        & > div {
          padding-right: 35px;
        }
      }
      .item_desc {
        opacity: 0;
        z-index: 3;
        position: absolute;
        left: 30px;
        bottom: 40px;
        font-family: Poppins;
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        color: rgba(158, 154, 188, 1);
      }
    }
  }
  .list_wrap {
    .list {
      &:nth-child(1) {
        .item {
          &:nth-child(1) {
            .item_icon {
              background-image: url('../../../assets/images/home/develop/g1_0.png');
            }
            .item_card_icon1 {
              z-index: 1;
              position: absolute;
              right: 0;
              bottom: 0;
              width: 100%;
              height: 100%;
              background-image: url('../../../assets/images/home/develop/g1_1.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
            }
            .item_card_icon2 {
              z-index: 2;
              position: absolute;
              left: 44px;
              top: 20px;
              width: 77px;
              height: 81px;
              background-image: url('../../../assets/images/home/develop/g1_2.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon3 {
              z-index: 3;
              position: absolute;
              right: 42px;
              top: 108px;
              width: 33px;
              height: 21px;
              background-image: url('../../../assets/images/home/develop/g1_3.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            &:hover {
              .item_card_icon2 {
                left: 58px;
              }
              .item_card_icon3 {
                right: 60px;
              }
            }
          }
          &:nth-child(2) {
            .item_icon {
              background-image: url('../../../assets/images/home/develop/g2_0.png');
            }
            .item_card_icon1 {
              z-index: 2;
              position: absolute;
              right: 0;
              bottom: 0;
              width: 100%;
              height: 100%;
              background-image: url('../../../assets/images/home/develop/g2_1.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
            }
            .item_card_icon2 {
              z-index: 1;
              position: absolute;
              left: 16px;
              top: 54px;
              width: 75px;
              height: 47px;
              background-image: url('../../../assets/images/home/develop/g2_2.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon3 {
              z-index: 3;
              position: absolute;
              right: 26px;
              top: 80px;
              width: 45px;
              height: 45px;
              background-image: url('../../../assets/images/home/develop/g2_3.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon4 {
              z-index: 4;
              position: absolute;
              left: 52px;
              bottom: 20px;
              width: 72px;
              height: 55px;
              background-image: url('../../../assets/images/home/develop/g2_4.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            &:hover {
              .item_card_icon2 {
                left: 28px;
              }
              .item_card_icon3 {
                right: 36px;
              }
              .item_card_icon4 {
                left: 70px;
              }
            }
          }
        }
      }
      &:nth-child(2) {
        .item {
          &:nth-child(1) {
            .item_icon {
              background-image: url('../../../assets/images/home/develop/g3_0.png');
            }
            .item_card_icon1 {
              z-index: 2;
              position: absolute;
              right: 0;
              bottom: 0;
              width: 100%;
              height: 100%;
              background-image: url('../../../assets/images/home/develop/g3_1.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
            }
            .item_card_icon2 {
              z-index: 3;
              position: absolute;
              left: 77px;
              bottom: 65px;
              width: 53px;
              height: 53px;
              background-image: url('../../../assets/images/home/develop/g3_2.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon3 {
              z-index: 3;
              position: absolute;
              right: 29px;
              bottom: 70px;
              width: 50px;
              height: 49px;
              background-image: url('../../../assets/images/home/develop/g3_3.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }

            &:hover {
              .item_card_icon2 {
                left: 70px;
                bottom: 64px;
                width: 69px;
                height: 69px;
              }
              .item_card_icon3 {
                right: 50px;
                bottom: 67px;
              }
            }
          }
          &:nth-child(2) {
            .item_icon {
              background-image: url('../../../assets/images/home/develop/g4_0.png');
            }
            .item_card_icon1 {
              z-index: 1;
              position: absolute;
              right: 0;
              bottom: 0;
              width: 100%;
              height: 100%;
              background-image: url('../../../assets/images/home/develop/g4_1.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
            }
            .item_card_icon2 {
              z-index: 2;
              position: absolute;
              left: 0;
              top: 70px;
              width: 46px;
              height: 31px;
              background-image: url('../../../assets/images/home/develop/g4_2.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon3 {
              z-index: 3;
              position: absolute;
              left: 75px;
              top: 117px;
              width: 54px;
              height: 36px;
              background-image: url('../../../assets/images/home/develop/g4_3.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            .item_card_icon4 {
              z-index: 4;
              position: absolute;
              right: 15px;
              top: 101px;
              width: 54px;
              height: 26px;
              background-image: url('../../../assets/images/home/develop/g4_4.png');
              background-size: 100% 100%;
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }
            &:hover {
              .item_card_icon2 {
                left: 20px;
              }
              .item_card_icon3 {
                left: 52px;
              }
              .item_card_icon4 {
                right: 40px;
              }
            }
          }
        }
      }
    }
  }
}

.developActive {
  .title {
    animation: homeFadeInUp 0.8s ease-in-out forwards;
  }
  .sub_title {
    animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
  }
}

.develop {
  .listActive {
    .item {
      animation: homeFadeIn 0.8s ease-in-out 0s forwards;
      .item_title {
        animation: homeFadeInUp 0.8s ease-in-out 0.4s forwards;
      }
      .item_desc {
        animation: homeFadeInUp 0.8s ease-in-out 1.2s forwards;
      }
    }
  }
}

@include mobile-only {
  .title {
    font-size: 24px;
    line-height: 36px;
    span {
      &:first-child {
        &::before {
          left: -34px;
          top: -14px;
          width: 30px;
          height: 27px;
        }
      }
    }
    .title_end {
      &::after {
        bottom: -22px;
      }
    }
  }
  .sub_title {
    margin-top: 10px;
    font-size: 14px;
    line-height: 21px;
  }

  .develop {
    .list_wrap {
      margin-top: 30px;
      .list {
        grid-template-columns: 1fr;
        gap: 10px;
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
        &:nth-child(1) {
          .item {
            height: 220px;
            animation: none;
            opacity: 1;
            .item_card {
              right: 0;
              bottom: initial;
              top: 0;
              width: 208px;
              height: 160px;
            }
            .item_title {
              left: 20px;
              bottom: 52px;
              font-size: 18px;
              line-height: 27px;
              & > div {
                padding-right: 35px;
              }
            }
            .item_desc {
              left: 20px;
              bottom: 20px;
            }
            .item_card_icon2,
            .item_card_icon3,
            .item_card_icon4 {
              display: none;
            }
            &:nth-child(1) {
              .item_card_icon1 {
                background-image: url('../../../assets/images/home/develop/m_1.png');
              }
            }
            &:nth-child(2) {
              .item_card_icon1 {
                background-image: url('../../../assets/images/home/develop/m_2.png');
              }
            }
          }
        }
        &:nth-child(2) {
          .item {
            height: 220px;
            animation: none;
            opacity: 1;
            .item_card {
              right: 0;
              bottom: initial;
              top: 0;
              width: 208px;
              height: 160px;
            }
            .item_title {
              left: 20px;
              bottom: 52px;
              font-size: 20px;
              line-height: 30px;
              & > div {
                padding-right: 35px;
              }
            }
            .item_desc {
              left: 20px;
              bottom: 20px;
            }
            .item_card_icon2,
            .item_card_icon3,
            .item_card_icon4 {
              display: none;
            }
            &:nth-child(1) {
              .item_card_icon1 {
                background-image: url('../../../assets/images/home/develop/m_3.png');
              }
            }
            &:nth-child(2) {
              .item_card_icon1 {
                background-image: url('../../../assets/images/home/develop/m_4.png');
              }
            }
          }
        }
        .item {
          position: relative;
          // background: rgba(24, 18, 40, 1);
          border: 1px solid rgba(255, 255, 255, 0.1);
          background: linear-gradient(122.68deg, rgba(19, 6, 46, 0.5) 14.19%, rgba(26, 14, 44, 0.5) 85.09%);
          border-radius: 10px;
          // &:before {
          //   content: '';
          //   position: absolute;
          //   inset: 0; /*  */
          //   padding: 1px; /*  */
          //   border-radius: 10px; /*  */
          //   background: linear-gradient(181.14deg, rgba(255, 255, 255, 0.3) 25.9%, rgba(255, 255, 255, 0.1) 76.35%);
          //   -webkit-mask: linear-gradient(#fff, #fff) content-box, /*  */ linear-gradient(#fff, #fff); /*  */
          //   -webkit-mask-composite: xor; /*  */
          //   mask-composite: exclude; /*  Firefox */
          //   z-index: 1; /*  */
          // }
        }
      }
    }
  }
}
