@import '../../../assets/css/var.scss';
@import '../../../assets/css/mixins';
@import '../animation.scss';

.dayStatus {
  opacity: 0;
  .list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
  }
  .item {
    position: relative;
    height: 168px;
    background: url('../../../assets/images/home/dayStatus/bg_up.png') no-repeat;
    background-size: 100% 100%;
    background-position: 0 0;
    // border: 1px solid rgba(255, 255, 255, 0.1);
    // border-radius: 10px;
    &.item_down {
      background-image: url('../../../assets/images/home/dayStatus/bg.png');
    }
    // &:hover {
    //   background-image: url('../../../assets/images/home/dayStatus/bg_active.png');
    //   .icon {
    //     background: rgba(70, 49, 104, 1);
    //     img {
    //       opacity: 1;
    //     }
    //   }
    // }
    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 20px;
      right: 20px;
      width: 30px;
      height: 30px;
      border-radius: 4px;
      background: rgba(41, 31, 55, 1);
      img {
        display: block;
        width: 24px;
        height: 24px;
        opacity: 0.5;
      }
      &:nth-child(3) {
        img {
          width: 20px;
          height: 20px;
        }
      }
    }
  }
  .item_main {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .item_title {
    display: inline-block;
    padding: 26px 20px 0 20px;
    font-family: Poppins;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: rgba(167, 162, 201, 1);
    user-select: none;
    & > div {
      padding-right: 18px;
    }
    .aniArrow_m {
      display: none;
      width: 8px;
      height: 8px;
      img {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }
  .item_desc {
    display: inline-flex;
    margin-top: 20px;
    padding: 0 20px;
    width: 100%;
    font-family: Poppins;
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    user-select: none;
  }
  .total {
    font-family: Poppins-Bold;
    font-weight: 700;
    margin-right: 10px;
    color: #fff;
    user-select: none;
  }
  .rate {
    font-family: Poppins;
    font-weight: 400;
    font-size: 14px;
    line-height: 36px;
    color: rgba(0, 255, 150, 1);
    user-select: none;
  }
  .rate_down {
    color: rgba(255, 50, 135, 1);
  }
  .chart {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}

.dayStatusInview {
  animation: homeFadeIn 1s ease-in-out forwards;
}

@include mobile-only {
  .dayStatus {
    .list {
      grid-template-columns: repeat(2, 1fr);
      gap: 15px;
    }
    .item_main {
      z-index: 2;
    }
    .item {
      height: 140px;
      background-image: url('../../../assets/images/home/dayStatus/bg_m_up.png') !important;
      border: 1px solid rgba(255, 255, 255, 0.1);
      border-radius: 10px;
      &.item_down {
        background-image: url('../../../assets/images/home/dayStatus/bg_m.png') !important;
      }
      .icon {
        top: 10px;
        left: 10px;
        width: 24px;
        height: 24px;
        img {
          width: 19px;
          height: 19px;
        }
        &:nth-child(3) {
          img {
            width: 16px;
            height: 16px;
          }
        }
      }
    }

    .item_title {
      display: flex;
      align-items: center;
      padding: 10px 10px 0 10px;
      font-size: 12px;
      line-height: 18px;
      & > div {
        padding-right: 0;
        &:first-child {
          &::after {
            display: none;
          }
          &::before {
            display: none;
          }
        }
      }
      .aniArrow_m {
        display: block;
        margin-left: 8px;
      }
    }
    .item_desc {
      display: block;
      margin-top: 10px;
      padding: 0 10px;
      font-size: 16px;
      line-height: 24px;
    }
    .total {
      margin-right: 0;
    }
    .rate {
      margin-top: 4px;
      font-size: 12px;
      line-height: 18px;
    }
    .chart {
      z-index: 1;
    }
  }
}
